import React from 'react';
import Layout from '../components/Layout'
import '../components/Layout.css'
import p1 from "../resource/p1.png"
import p2 from "../resource/p2.png"
import p3 from "../resource/p3.png"

const page1 = ({location}) => {
  return (
    <Layout>
      <h1>List of Producst</h1>
      <div>
        <a href="../p1" target="_self" rel="noreferrer" >
      <button class="button"> 
      <div class="listGrid">
        <div>
          <img src= {p1}  class="image100"></img>
        </div>
        <div>
          <h3>PartID : 1</h3>
          <p>Part of a compressor with id of 1</p>
        </div>
      </div> 
      </button>
      </a>
      <hr class="gray"></hr>

      <a href="../p2" target="_self" rel="noreferrer">
      <button href="./products/p2" target="_self" rel="noreferrer" class="button"> 
      <div class="listGrid">
        <div>
          <img src= {p2}  class="image100"></img>
        </div>
        <div>
          <h3>PartID : 2</h3>
          <p>a full compressor of model 2</p>
        </div>
      </div> 
      </button>
      </a>
      <hr class="gray"></hr>

      <a href="../p3" target="_self" rel="noreferrer">
      <button href="./products/p3" target="_self" rel="noreferrer" class="button"> 
      <div class="listGrid">
        <div>
          <img src= {p3}  class="image100"></img>
        </div>
        <div>
          <h3>PartID : 3 </h3>
          <p>pully system of compressor mk3</p>
        </div>
      </div> 
      </button>
      </a>
      <hr class="gray"></hr>
      </div>
    </Layout>
  )
}

export default page1;